import React from 'react';

class PriceCard extends React.Component {
 constructor({tier,cost,noOfUsers,storage,count,domain}){
  super();
  this.positive =[]
  this.negative =[]
  this.state = {
    tier: tier,
    cost: cost,
    count: count,
    details: [`${noOfUsers} User`,`${storage} GB Storage`,
      'Unlimited Public Projects',
      'Community Access',
      'Unlimited Private Projects',
      'Dedicated Phone Support',
      `${domain} Free Subdomain`,
      'Monthly Status Reports',
    ],
    
  }
 }
 render() {
  for(let i = 0; i <this.state.count;i++){
   this.positive.push(this.state.details[i])
  }
  for(let i = this.state.count;i<this.state.details.length;i++){
   this.negative.push(this.state.details[i])
  }
  return (
    <div className='col-lg-4'>
      <div className='card mb-5 mb-lg-0'>
        <div className='card-body'>
          <h5 className='card-title text-muted text-uppercase text-center'>
            {this.state.tier}
          </h5>
          <h6 className='card-price text-center'>
            ${this.state.cost}
            <span className='period'>/month</span>
          </h6>
          <hr></hr>
          <ul className='fa-ul'>
            {this.positive.map((detail, index) => (
              <li key={index}>
                <span className='fa-li'>
                  <i className='fas fa-check'></i>
                </span>
                {detail}
              </li>
            ))}
            {this.negative.map((detail, index) => (
              <li key={index} className='text-muted'>
                <span className='fa-li'>
                  <i className='fas fa-times'></i>
                </span>
                {detail}
              </li>
            ))}
          </ul>
          <a href='./index.html' className='btn btn-block btn-primary text-uppercase'>
            Button
          </a>
        </div>
      </div>
    </div>
  )
 }
}

export default PriceCard;